html {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 17px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0; }

.page-header {
  position: relative;
  max-width: 1366px;
  margin: 0 auto;
  min-height: 906px;
  background-image: url("../../img/header.png");
  background-repeat: no-repeat;
  background-position: center 0; }
  @media (max-width: 320px) {
    .page-header {
      background-image: url("../../img/header-mobile.png");
      min-height: 617px;
      background-position: center 10px; } }

.main-menu {
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.2;
  position: relative;
  padding-top: 48px;
  padding-left: 15px;
  padding-right: 15px;
  max-width: 1142px;
  margin: 0 auto 12px auto; }
  @media (max-width: 767px) {
    .main-menu {
      padding-top: 20px;
      font-size: 14px; } }

.main-menu a {
  color: unset;
  text-decoration: none; }

.main-menu__logo {
  position: absolute;
  left: 0;
  right: 0;
  top: 32px;
  margin: auto;
  width: 137px;
  height: 48px;
  background-image: url("../../img/logo.png");
  background-repeat: no-repeat;
  background-size: contain; }
  @media (max-width: 767px) {
    .main-menu__logo {
      right: auto;
      top: 20px;
      left: 15px;
      width: 120px;
      height: 42px; } }

.main-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }
  @media (max-width: 767px) {
    .main-menu__list {
      display: block;
      text-align: right; } }

.main-menu__item--call {
  font-variant: small-caps;
  border-bottom: 1px dashed currentColor; }
  @media (max-width: 767px) {
    .main-menu__item--call {
      margin-top: 8px;
      display: inline-block; } }

.intro {
  padding-top: 446px; }
  @media (max-width: 767px) {
    .intro {
      padding-top: 272px; } }

.intro p {
  font-weight: bold;
  font-size: 24px;
  line-height: 1.2;
  text-align: center;
  max-width: 343px;
  margin: 0 auto 32px auto;
  color: #ffffff;
  text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12); }
  @media (max-width: 767px) {
    .intro p {
      font-size: 20px; } }

.intro__scroll {
  display: block;
  margin: 0 auto;
  max-width: 76px;
  min-height: 131.5px;
  background-image: url("../../img/icon-mouseScroll.svg"); }
